<template>
  <el-dialog
    class="custom-dialog"
    width="450px"
    title="新增分组"
    :model-value="initData.visible"
    @close="handleClose"
  >
    <el-form
      class="form"
      ref="formRef"
      :model="formData"
      :rules="rules"
      label-width="100px"
    >
      <el-form-item label="分组名称" prop="groupName">
        <el-input
          v-model="formData.groupName"
          maxlength="10"
          show-word-limit
        ></el-input>
      </el-form-item>
    </el-form>
    <template #footer>
      <div class="dialog-footer">
        <el-button @click="handleClose">取消</el-button>
        <el-button type="primary" @click="submit">确定</el-button>
      </div>
    </template>
  </el-dialog>
</template>
<script lang="ts" setup>
import { ref, shallowReactive } from "vue";
import { ElMessage } from "element-plus";

import { useVueRouter } from "@assets/useHook";
import { addSpeechLibraryGroupApi } from "@/api/business-management-api";

const emits = defineEmits<{
  (e: "emitFunc"): void;
}>();

const { routeQuery } = useVueRouter();
const formRef = ref();
const initData = shallowReactive({
  visible: false,
});
const formData = shallowReactive({
  groupName: "",
});
const rules = {
  groupName: [
    {
      required: true,
      message: "请输入分组名称",
      trigger: "blur",
    },
  ],
};
const handleClose = () => {
  formRef.value.resetFields();
  initData.visible = false;
};
const submit = () => {
  formRef.value.validate(async (valid: boolean) => {
    if (valid) {
      const params = {
        profileId: routeQuery.value.profileId,
        companyId: routeQuery.value.companyId,
        name: formData.groupName,
        parentId: 0,
      };
      await addSpeechLibraryGroupApi.getData(params);
      ElMessage.success("添加分组成功");
      handleClose();
      emits("emitFunc");
    } else {
      return false;
    }
  });
};
function trigger(visible: boolean) {
  initData.visible = visible;
}
defineExpose({
  trigger,
});
</script>

<style lang="scss" scoped>
.verification {
  position: relative;

  :deep(.el-input__wrapper) {
    padding-right: 95px;
  }
  .picture-code,
  .telephone-code {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    right: 0;
    width: 90px;
    height: 40px;
    font-size: 14px;
    color: #0056ff;
    cursor: pointer;

    span {
      height: 100%;
    }
    img {
      width: 90%;
    }
  }
}

:deep(.el-input__inner:focus) {
  border-color: #0056ff;
}
</style>
